import MODULE_ID from '~./src/module'

export function localize(key: string, data?: Record<string, string>) {
    key = `${MODULE_ID}.${key}`
    if (data) return game.i18n.format(key, data)
    return game.i18n.localize(key)
}

export function subLocalize(subKey: string) {
    return (key: string, data: Record<string, string>) => localize(`${subKey}.${key}`, data)
}
